import { gql  } from '@apollo/client'

export const GET_SPLITTED_LIST = gql`
query {
  getSplittedList  {
    leftList {
      id
      name
    }
    rightList {
      id
      name
    }
  }
}
`

export const GET_COVERED = gql`
query {
  getCovered  {
    id
    name
  }
}
`

export const CREATE_TOPIC = gql`
mutation createTopic($name: String!) {
  addTopic(
    name: $name,
  ) {
    id
    name
  }
}
`
